import { unitsConverter } from '@ali/4ever-utils';

export function parseBorder(border: string) {
  const measureDom = document.createElement('div');
  if (border) {
    measureDom.style.border = border;
  }
  const { borderWidth, borderStyle, borderColor } = measureDom.style;
  return {
    sz: unitsConverter.toPT(borderWidth),
    type: borderStyle,
    color: borderColor,
  };
}
